.uxora-footer{
  &-section{
    padding: 130px 0 50px;
    @include max (991px){
      padding: 80px 0 40px;
    }
    @include max (767px){
      padding: 60px 0 30px;
    }
  }
  &-one{
    padding: 80px 80px 0 80px;
    background-color: var(--light-color);
    border-radius: 20px;
    margin-top: 100px;
    @include max (1199px){
      padding: 60px 60px 0 60px;
    }
    @include max (991px){
      margin-top: 80px;
      padding: 40px 40px 0 40px;
    }
    @include max (767px){
      margin-top: 60px;
      padding: 30px 30px 0 30px;
    }
  }
  &-menu{
    &.menu1{
      @include max (767px){
        padding-bottom: 30px;
      }
    }
    h3{
      margin-bottom: 24px;
    }
    ul{
      li{
        color: var(--heading-color);
        margin-bottom: 10px;
        &:last-child{
          margin-bottom: 0;
        }
        a{
          position: relative;
          transition: all 0.4s;
          font-size: 21px;
          line-height: 30px;
          color: var(--heading-color);
          &::before{
            content: " ";
            left: 0;
            bottom: -3px;
            width: 0;
            height: 1px;
            position: absolute;
            transition: all .4s;
            background: var(--heading-color);
          }
          &:hover::before{
            width: 100%;
          }
        }
      }
    }
    &.menu2{
      padding-left: 30px;
      @include max (1399px){
        padding-left: 70px;
      }
      @include max (1199px){
        padding-left: 0;
      }
    }
    &.menu3{
      padding-left: 65px;
      @include max (1399px){
        padding-left: 90px;
      }
      // @include max (1199px){
      //   padding-left: 80px;
      // }
      @include max (1199px){
        padding-left: 0;
      }
    }
  }
  &-textarea{
    padding-right: 15px;
    img{
      padding-bottom: 24px;
    }
    p{
      font-size: 21px;
      font-weight: 400;
      line-height: 30px;
    }
    @include max (1399px){
      margin-bottom: 50px;
      max-width: 500px;
    }
    @include max (991px){
      margin-bottom: 40px;
    }
    @include max (767px){
      margin-bottom: 30px;
    }
   
  }
  &-bottom-wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 80px;
    padding: 30px 0;
    border-top: 1px solid #1405331A;
    @include max (991px){
      margin-top: 60px;
      display: block;
      text-align: center;
    }
  }
  &-bottom-text{
    p{
      @include max (991px){
        margin-bottom: 15px;
      }
    }
  }
}
.uxora-conatct{
  &-info{
    ul{
      li{
        margin-bottom: 10px;
        &:last-child{
          margin-bottom: 0;
        }
        span{
          font-weight: 600;
        }
        a{
          color: var(--heading-color);
          span{
            font-weight: 600;
          }
        }
      }
    }
  }
}
.pl-60{
  padding-left: 60px;
  @include max (1399px){
    padding-left: 0;
  }

  @include max (767px){
    padding-left: 0;
  }
}
.pl-41{
  padding-left: 40px;
  @include max (1399px){
    padding-left: 0;
    padding-top: 40px;
  }
  @include max (767px){
    padding-left: 0;
   
  }
}
.pl-42{
  @include max (1199px){
    padding-left: 40px;
  }
  @include max (767px){
    padding-left: 0;
  }
}

.pl-30{
  padding-left: 30px;
  @include max (1199px){
    padding-left: 0;
  }
}