.uxora-about-us{
    &-thumb{
        img{
            width: 100%;
        }
        @include max (991px){
            padding-bottom: 40px;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
        &.thumb2{
            position: relative;
            text-align: right;
            img{
                border-radius: 10px;
            }
        }
    }
}

.features-section{
    border-radius: 50px;
    background-color: var(--light-color2);
}
.uxora-features-card {
    position: absolute;
    bottom: 80px;
    left: 40px;
}

.mt-30{
    margin-top: 30px;
}

@keyframes updown {
	0% {
		transform: translatey(0px);
	}
	50% {
		transform: translatey(-30px);
	}
	100% {
		transform: translatey(0px);
	}
}

.one {
    top: 130px;
    @include max (991px){
        margin-top: 40px;
    }
    @include max (767px){
        margin-top: 30px;
    }
}


.two {
    top: 80px;
}
.three {
    top: 110px;
}

.four {
    top: 140px;
}
.fivr {
    top: 160px;
}
.six {
    top: 130px;
}

.seven {
    top: 110px;
}
.eight {
    top: 120px;
}
.pl-101{
    padding-left: 100px;
    @include max (1199px){
        padding-left: 50px;
    }
    @include max (991px){
        padding-left: 0;
    }
}
.pb-130{
    padding-bottom: 130px;
    position: relative;
    transition: all 0.4s;
    @include max (991px){
        padding-bottom: 80px;
    }
    @include max (767px){
        padding-bottom: 60px;
    }
}
.pb-131{
    padding-bottom: 100px;
    position: relative;
    transition: all 0.4s;
    @include max (991px){
        padding-bottom: 80px;
    }
    @include max (767px){
        padding-bottom: 60px;
    }
    .border-r{
        &::before{
            height: 360px;
          
        }
    }
}

.border-r{
    position: relative;
    &::before{
        top: 30px;
        left: 52.5%;
        width: 1px;
        height: 520px;
        content: "";
        position: absolute;
        background-color: #0A0F0A1A;
        @include max (1399px){
            left: 54.5%;
            height: 580px;
        }
        @include max (1399px){
            display: none;
        }
    }
}

.uxora-features{
    &-number{
        position: absolute;
        top: 20px;
        z-index: 1;
        left: 50%;
        width: 60px;
        height: 60px;
        display: flex;
        align-items: center;
        border-radius: 100px;
        justify-content: center;
        transition: all .4s;
        background-color: var(--accent-bg);
        @include max (1399px){
            left: 52%;
        }
        @include max (1399px){
            display: none;
        }
    }
    &-title{
        color: var(--light-color);
    }
    &-dashboard{
        padding-bottom: 80px;
        @include max (767px){
            padding-bottom: 60px;
        }
    }
}