.uxora-section-padding{
    padding: 130px 0;
    @include max (991px){
        padding: 80px 0;
    }
    @include max (767px){
        padding: 60px 0;
    }
}
.uxora-section-padding2{
    padding: 130px 0 106px;
    @include max (991px){
        padding: 80px 0 56px;
    }
    @include max (767px){
        padding: 60px 0 36px;
    }
}
.uxora-section-padding3{
    padding: 0 0 106px;
    @include max (991px){
        padding: 0 0 76px;
    }
    @include max (767px){
        padding: 0 0 36px;
    }
}
.uxora-section-padding4{
    padding: 0 0 130px;
    @include max (991px){
        padding: 0 0 80px;
    }
    @include max (767px){
        padding: 0 0 60px;
    }
}
.uxora-section-padding5{
    padding: 195px 0 130px;
    @include max (991px){
        padding: 150px 100px;
    }
    @include max (767px){
        padding:130px 0 80px;
    }
}
.uxora-section-padding6{
    padding: 130px 0 0;
    @include max (991px){
        padding: 80px 0 0;
    }
    @include max (767px){
        padding:60px 0 0;
    }
}


// section title

.uxora-section-title{
    padding-bottom: 80px;
    @include max (991px){
        padding-bottom: 60px;
        text-align: center;
    }
    @include max (767px){
        padding-bottom: 40px;
    }
    &.max-660{
        max-width: 660px;
    }
    &.max-700{
        max-width: 750px;
    }
   
    &.center{
        max-width: 650px;
        margin: 0 auto;
        text-align: center;
        @include max(1199px) {
            max-width: 600px;
        }
        &.max-width-700{
            max-width: 700px;
            @include max(1199px) {
                max-width: 600px;
            }
        }
        &.max-width-755{
            max-width: 755px;
            @include max(1199px) {
                max-width: 600px;
            }
        }
        &.max-width-800{
            max-width: 800px;
        }
    }
}
.uxora-default{
    &-data {
      font-size: 21px;
      line-height: 30px;
    }
  }
// default content
.uxora-default-content{
    h2{
        padding-bottom: 20px;
    }
    h3{
        padding-bottom: 15px;
    }
    &.content2{
        h2, p{
            color: var(--light-color);
        }
        padding-right: 50px;
        @include max (1199px){
            padding-right: 0;
        }
        @include max (991px){
            padding-bottom: 40px;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
    }&.faq-content{
        padding-left: 100px;
        h3{
            padding-bottom: 0;
        }
        @include max (1399px){
            padding-left: 60px;
        }
        @include max (1199px){
            padding-left: 30px;
        }
        @include max (991px){
            padding-left: 0;
        }
    }
}
.pl-0{
    padding-left: 0!important;
}
.title{
    padding-bottom: 20px;
}
.mt-50{
    margin-top: 50px;
    @include max (991px){
        margin-top: 40px;
    }
    @include max (767px){
        margin-top: 30px;
    }
}
.mt-30{
    margin-top: 30px;
    @include max (767px){
        margin-top: 24px;
    }
}
.section{
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.padding-tb-60{
    overflow: hidden;
    padding: 60px 0;
}