.uxora-faq{
  &-wrap1{
    padding-left: 70px;
    display: flex;
    flex-direction: column;
    gap: 24px;
    @include max (1199px){
      padding-left: 20px;
    }
    @include max (991px){
      padding-left: 0;
      padding-top: 40px;
    }
    @include max (767px){
      padding-top: 30px;
    }
    &.wrap2{
      padding-left: 0;
      padding-top: 0;
    }
  }
  &-header{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &-item{
    position: relative;
    cursor: pointer;
    padding: 30px;
    border-radius: 20px;
    background-color: var(--white-bg);
    &.open {
      position: relative;
      .uxora-faq-header{
        padding-bottom: 15px;
      }
      .uxora-faq-body{
        display: block;
      }
    }
    &.item2{
      padding: 0 0 0 20px;
      background-color: var(--light-color);
      &.open{
        &::after{
          opacity: 1;
        }
      }
      &::before{
        width: 4px;
        height: 100%;
        border-radius: 7px;
        top: 0;
        left: 0;
        content: "";
        position: absolute;
        background-color: #14053333;
      }
      &::after{
        width: 4px;
        height: 100%;
        border-radius: 0;
        top: 0;
        left: 0;
        content: "";
        position: absolute;
        opacity: 0;
        background-color: var(--heading-color);
      }
    }
  }
  &-body{
    display: none;
    max-width: 850px;
    &.body2{
      max-width: 980px;
    }
  }
}

img.plasicon{
  transition: all 0.4s;
}
.uxora-faq-item.open {
  img.plasicon{
    transform: rotate(45deg);
  }

}
.border-b{
  padding-bottom: 0;
  border-bottom: none;
  margin-bottom: 0;
}


