input[type="text"],
input[type="email"],
input[type="password"],
input[type="url"],
input[type="tel"],
input[type="time"],
input[type="date"],
input[type="search"],
input, textarea {
  margin: 0;
  width: 100%;
  height: 60px;
  color:var(--heading-color);
  border-radius: 0;
  background-color: transparent;
  padding: 15px 0 ;
  border: none;
  transition: all 0.4s;
}
textarea {
   height: 145px;
   resize: none;
   border-radius: 10px;
   background-color: var(--light-color);
   padding: 20px;
   @include max (767px){
      margin-bottom: 20px;
   }
   &::placeholder{
      color: var(--heading-color)!important;
   }
}


.uxora-main-field {
   position: relative;
   margin-bottom: 15px;
   input {
      border-radius: 50px;
      padding: 18px 24px 18px 24px;
      background-color: var(--light-color);
      &::placeholder{
         color: var(--heading-color)!important;
      }
   }
}
.sub-btn{
   position: absolute !important;
   top: 6px;
   right: 5px;
   padding: 11px 24px !important;
}