.uxora-hero{
  &-section{
    padding: 190px 0 360px;
    position: relative;
    @include max (991px){
      padding: 190px 0 280px;
    }
    @include max (767px){
      padding: 130px 0 200px;
    }
    @include max (470px){
      padding: 130px 0 150px;
    }
  }
  &-content{
    max-width: 1050px;
    text-align: center;
    margin: 0 auto;
    p{
      font-size: 21px;
      line-height: 30px;
    }
  }
  &-dashboard {
    margin-top: -21%;
    position: relative;
    @include max (1399px){
      margin-top: -25%;
    }
    @include max (1199px){
      margin-top: -30%;
    }
  }
}
