.uxora-t{
    &-wrap{
        h3{
            padding-bottom: 15px;
        }
    }
    &-ratting{
        padding-bottom: 24px;
    }
    &-thumb{
        @include max (1199px){
            padding-right: 40px;
        }
        @include max (991px){
            padding-bottom: 40px;
            padding-right: 0;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
        img{
            border-radius: 20px;
            @include max(991px) {
                width: 100%;
            }
        }
    }
    &-author{
        max-width: 300px;
        &-title{
            padding-bottom: 24px;
            border-bottom: 1px solid #1405331A;
            margin-bottom: 24px;
            p span{
                font-weight: 600;
            }
        }
    }
    &-overlay {
        position: absolute;
        top: 0;
        left: 0;
        @include max (991px){
            display: none;
        }
    }
}
.uxora-t-slider-js-init .uxora-t-next, 
.uxora-t-slider-js-init .uxora-t-prev {
    left: -25px;
    width: 60px;
    height: 60px;
    bottom: 50%;
    z-index: 9;
    display: block;
    cursor: pointer;
    position: absolute;
    background-size:25px;
    background-position: center;
    background-repeat: no-repeat;
    transition: all .4s;
    background-image: url(../images/testimonial/arrow.svg);
    border: none;
    background-color: var(--heading-color);
    border-radius: 100px;
    transform: rotate(180deg);
    @include max(991px) {
        display: none!important;
    }
  
}
.uxora-t-slider-js-init .uxora-t-prev {
    left: auto;
    right: -25px;
    background-image: url(../images/testimonial/arrow.svg);
    background-color: var(--heading-color);
    transform: rotate(0);
}

.uxora-t-slider-js-init {
    background-color: var(--light-color2);
    padding: 50px;
    border-radius: 20px;
    @include max (767px){
        padding: 30px;
    }
}
