
.uxora-contact{
    &-box{
        padding: 30px;
        max-width: 910px;
        margin: 0 auto;
        background-color: var(--white-color);
        border-radius: 20px;
        p{
            font-weight: 700;
            margin-bottom: 10px;
        }
    }
    &-info-wraper{
        max-width: 910px;
        margin: 0 auto;
    }
    &-info-wrap{
        background: var(--white-bg);
        border-radius: 20px;
        padding: 30px;
        display: flex;
        gap: 24px;
        margin-top: 24px;
    }
    &-info-content{
        h3{
            padding-bottom: 15px;
        }
        a{
            display: table;
            color: var(--heading-color);
        }
    }
}