.uxora-content{
    &-shape {
        position: absolute;
        bottom: -40px;
        right: 30%;
        @include max (1399px){
            display: none;
        }
    }
    &-shape2{
        position: absolute;
        bottom: -30px;
        right: 10px;
        @include max (1399px){
            bottom: 15px;
        }
        @include max (1199px){
            bottom: -44px;
            right: 60px;
        }
        @include max (991px){
            bottom: 38%;
            right: 50px;
        }
        @include max (767px){
            display: none;
        }
    }
}
//  add pricing

.uxora-pricing-shape {
    position: absolute;
    top: 0;
    right: 0;
}

.uxora-contact-shape {
    margin: 80px 0 auto;
    text-align: center;
    @include max (991px){
        margin: 60px 0 auto;
    }
    @include max (767px){
        margin: 40px 0 auto;
    }
    @include max (575px){
        margin: 30px 0 auto;
    }
}