
.uxora-content{
  &-thumb{
    img{
      width: 100%;
    }
    @include max (1199px){
      padding-right: 40px;
    }
    @include max (991px){
      padding-bottom: 40px;
      padding-right: 0;
    }
    @include max (767px){
      padding-bottom: 30px;
    }
    &.thumb2{
      text-align: right;
      @include max (1199px){
        padding-left: 40px;
      }
      @include max (991px){
        text-align: left;
        padding-left: 0;
      }
    }
  }
  &-btn{
    ul{
      gap: 20px;
      display: flex;
      flex-wrap: wrap;
      li{
        display: inline-block;
        
       
        a{
          font-size: 16px;
        font-weight: 600;
        line-height: 24px;
        transition: all 0.4s;
        padding: 15px 20px;
        border-radius: 50px;
        display: block;
          color: var(--heading-color);
          background-color: var(--light-color2);
          &:hover{
            background-color: var(--heading-color);
            color: var(--light-color);
            path{
              fill: var(--light-color);
              stroke: var(--light-color);
            }
          }
        }
        svg{
          margin-right: 10px;
          margin-top: -5px;
        }
       
      }
    }
  }
}
