
// counter

.uxora-counter{
  &-section{
    padding: 100px 0;
    position: relative;
    @include max (991px){
      padding: 80px 0;
    }
    @include max (767px){
      padding: 60px 0;
    }
  }
  &-wrap{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    text-align: left;
    gap: 60px;
    flex-wrap: wrap;
    margin-left: 120px;
    @include max (1399px){
      gap: 30px;
      margin-left: 60px;
    }
    @include max (991px){
      gap: 60px;
      margin-left: 0;
    }
    @include max (767px){
      display: block;
    }
  }
  &-item {
    @include max (767px){
      text-align: left;
      padding-bottom: 24px;
      &:last-child{
        padding-bottom: 0;
      }
    }
  }
  &-data {
    position: relative;
    display: flex;
    overflow: hidden;
    justify-content: start;
    align-items: center;
    height: 80px;
    @include max (1199px){
      height: 60px;
    }
    @include max (767px){
      height: 50px;
    }
    @include max (767px){
      justify-content: left;
    }
  }
  &-thumb{
    text-align: center;
    @include max (991px){
      padding: 40px 0;
      text-align: left;
    }
    img{
      border-radius: 20px;
      @include max (991px){
        width: 100%;
      }
    }
  }
}
.border-b2{
  border-bottom: 1px solid #1405331A;
}

.uxora-counter-data > span {
  z-index: 1;
  display: flex;
  flex-direction: column;
  height: 100%;
  transition: transform 2s ease;
  transform: translateY(0);
  line-height: 1;
  
}
.data2 > span{
  width: 15%;
  @include max (1199px){
    width: 10%;
  }
}
.uxora-counter-data > span span {
  flex: 0 0 100%;
  height: 100%;
}
.uxora-counter-data:nth-child(1) > span {
